body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.team-cnt{
	border:1px solid grey;
	margin:20px;
}
.teamName{
	background-color: #62d2a2;
	-webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.07);
	        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.07);
	padding: 10px;
	color: white;
}
.empDet{
	border: solid 1px #eef2f7;
	padding: 20px 0px;
}
.empName{
	font-weight: 500;
	font-family: sans-serif;
	font-size: 14px;
}
.empDes{
	font-weight: 200;
	font-family: sans-serif;
	font-size: 12px;
}
.cmpDet{
	display: -ms-flexbox;
	display: flex;
	padding: 10px;
}
.cmpDet div{
	margin:5px;
}


